html {
  box-sizing: border-box; }

*,
::before,
::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
  font-weight: 400;
  font-family: "Lato", Arial, Helvetica, sans-serif;
  color: #3d3d3d;
  background-color: #f3f3f3; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
  /* added line */ }

[hidden] {
  display: none !important; }

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 13px 23px;
  border: 0;
  border-radius: 20px;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 700;
  font-family: inherit;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background-color: #f22f49;
  transition: background-color 0.3s ease;
  outline: 0;
  cursor: pointer; }
  .button:disabled, .button--disabled {
    background-color: #919191;
    cursor: default;
    pointer-events: none; }
  .button:hover, .button:focus {
    background-color: #e00e2a; }

.button_invert {
  padding: 9px 22px;
  border-radius: 0;
  border: 2px solid #000;
  font-size: 17px;
  line-height: 1.2;
  color: #000;
  background-color: transparent; }
  .button_invert:hover, .button_invert:focus {
    color: #fff;
    background-color: #000; }

.input-text {
  width: 100%;
  display: block;
  height: 45px;
  padding: 13px 13px 12px;
  border: 1px solid transparent;
  border-radius: 5px;
  font: inherit;
  color: #000;
  background-color: #f3f3f3;
  outline: none; }
  .input-text:focus {
    border-color: #b6b6b6; }
  .input-text::placeholder {
    color: #a7a7a7; }

.input-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  padding: 0;
  border: 0;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #f10e34;
  outline: 0;
  cursor: pointer; }
  .input-button:disabled, .input-button--disabled {
    background-color: gray;
    cursor: default;
    pointer-events: none; }
  .input-button:hover, .input-button:focus {
    background-color: #c10b2a; }

.input-error {
  display: none;
  color: #f10e34; }

.input-error_visible {
  display: block; }

.form-control__message {
  margin-top: 8px; }

.header {
  color: #fff;
  background-color: #222222; }

.header__top {
  display: flex;
  justify-content: space-between;
  min-height: 115px; }

.header__user-menu {
