.nav__container {
  display: flex;
  justify-content: space-between;
  min-height: 51px; }

.nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  width: 100%;
  max-width: calc(100% - 25.39% - 39px);
  margin: 0;
  padding: 0; }

.nav__link {
  display: block;
  padding: 17px 0 16px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.3s ease; }
  .nav__link:hover {
    color: #ffc808; }

